import React from "react";
import Banner from "./Banner";
import Product from "./Product";
import Loading from "../../utils/Loading/Loading";
import Brand from "./Brand";
import Offer from "./Offer";
import PopularProduct from "./PopularProduct";
import Choose from "./Choose";
import Blog from "./Blog";

function Home() {
  return (
    <>
    <Loading/>
      <Banner />
      {/*<Brand/>*/}
      <Offer/>
      <PopularProduct/>
      <Choose/>
      <Product />
      <div className="newsletter-area-section-3 newsletter-area-section-bg pt-100">
        <div className="container ">
          <div className="row align-items-center">
            <div className="col-lg-6">
              <div className="section-title">
                <span>
                  Gim n 35%, <b> ng k ngay</b>
                </span>
                <h2>ng k thng bo thng tin v sn phm mi ca chng ti</h2>

              </div>
              <div className="newsletter-area-two newsletter-area-three">
                <form
                  className="newsletter-form"
                  data-toggle="validator"
                  method="POST"
                  noValidate="true"
                >
                  <input
                    type="email"
                    className="form-control"
                    placeholder="Nhp email ca bn..."
                    name="EMAIL"
                    required
                    autoComplete="off"
                  />
                  <button
                    className="subscribe-btn disabled"
                    type="submit"
                    style={{ pointerEvents: "all", cursor: "pointer" }}
                  >
                    ng k
                  </button>
                  <div id="validator-newsletter" className="form-result" />
                </form>
              </div>
            </div>
            <div className="col-lg-6">
              <div className="newsletter-img">
                <img src="assets/images/newsletter-img.jpg" alt="Images" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <Blog/>
      <div className="contact-area col-md-12 m-auto">
        <div className="row">
          <div className="col-lg-12 col-md-12">
            <div className="contact-card">
              <i className="bx bxs-map" />
              <h3>Ca hng ca chng ti trn ton quc :</h3>
              <p>CH1: S 37 Ng 109 ng Trng Chinh, Phng Lit, Thanh Xun, H Ni</p>
              <p>CH2: 143 . Trn ng Ninh, Hong Vn Th, Thnh ph Lng Sn, Lng Sn</p>
              <p>CH3: T 4 phng c Sn, TP.Bc Kn</p>
              <p>CH4: ng ni th 2 (cng ch chiu) Th trn Vng, Tin L, Hng Yn </p>
              <p>CH5: S 192 Phan nh Phng, TP Thi Nguyn </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Home;
