import React, { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { ToastContainer, toast } from 'react-toastify';
  import 'react-toastify/dist/ReactToastify.css';
import {
  isEmpty,
  isEmail,
  isLength,
  isMatch,
} from "../../utils/validation/Validation";
import Loading from '../../utils/Loading/Loading'
const initialState = {
  name: "",
  phoneNumber: "",
  accountPassword: "",
  userAddress: "",
  gmail: "",
};


function Register() {
  const [user, setUser] = useState(initialState);

  const { name, phoneNumber, accountPassword, userAddress, gmail } = user;

  const handleChangeInput = (e) => {
    const { name, value } = e.target;
    setUser({ ...user, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (isEmpty(name) || isEmpty(accountPassword) || isEmpty(phoneNumber)  || isEmpty(userAddress))
     toast.error('Vui lng in thng tin !');

    if (!isEmail(gmail))
      toast.error('Email khng hp l !');

    if (isLength(accountPassword))
    toast.error('Mt khu cha t nht 6 k t !');

    

    try {
      const res = await axios.post(process.env.REACT_APP_HOST +"/api/Accounts/register", {
        name, phoneNumber, accountPassword, userAddress, gmail
      });

      setUser(initialState);
      toast.success('ng k ti khon thnh cng!')
    } catch (err) {
      toast.error('ng k tht bi!')
    }
  };

  return (
    <>
    <ToastContainer
position="bottom-left"
autoClose={5000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover
/>

<Loading/>
{/* Same as */}

      <div>
        <div className="inner-banner-area">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-5 col-md-4">
                <div className="inner-content">
                  <h2>ng k</h2>
                  <ul>
                    <li>
                      <Link to="/">Trang ch</Link>
                    </li>
                    <li>ng k</li>
                  </ul>
                </div>
              </div>
              <div className="col-lg-7 col-md-8">
                <div className="user-area pt-100 pb-70">
                  <div className="container">
                    <div className="user-width">
                      <div className="user-form">
                        <div className="contact-form">
                          <h2>ng k</h2>
                          <form onSubmit={handleSubmit}>
                            <div className="row">
                              <div className="col-lg-12 ">
                                <div className="form-group">
                                  <input
                                      type="text"
                                      className="form-control"

                                      placeholder="Nhp tn ngi dng"
                                      name="name"
                                      onChange={handleChangeInput}
                                  />
                                </div>
                              </div>
                              <div className="col-lg-12 ">
                                <div className="form-group">
                                  <input
                                      type="text"
                                      className="form-control"

                                      placeholder="Nhp email..."
                                      name="gmail"
                                      onChange={handleChangeInput}
                                  />
                                </div>
                              </div>
                              <div className="col-12">
                                <div className="form-group">
                                  <input
                                      className="form-control"
                                      type="password"
                                      name="accountPassword"
                                      placeholder="Mt khu..."
                                      onChange={handleChangeInput}
                                  />
                                </div>
                              </div>
                              <div className="col-lg-12 ">
                                <div className="form-group">
                                  <input
                                      type="text"
                                      className="form-control"

                                      name="phoneNumber"
                                      placeholder="Nhp s in thoi..."
                                      onChange={handleChangeInput}
                                  />
                                </div>
                              </div>
                              <div className="col-lg-12 ">
                                <div className="form-group">
                                  <input
                                      type="text"
                                      className="form-control"

                                      name="userAddress"
                                      placeholder="Nhp a ch..."
                                      onChange={handleChangeInput}
                                  />
                                </div>
                              </div>
                              <div className="col-lg-6 ">
                                <Link to='/login' className="default-btn btn-bg-three">
                                  ng nhp
                                </Link>
                              </div>
                              <div className="col-lg-6 ">
                                <button type="submit" className="default-btn btn-bg-three">
                                  ng k
                                </button>
                              </div>
                            </div>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Register;
